.prize {
    margin-bottom: 65px;
    @include vertical;
    &__col {
        margin: 0 5px;
        display: inline-block;
        vertical-align: bottom; }
    &__name {
        text-align: right;
        &_start-double {
            min-width: 214px;
            font-size: 48px;
            color: #fff;
            line-height: 46px; }
        &-top_start-double {
            font-family: $myriadpro-cond;
            font-weight: 600; }
        &-btm_start-double {
            font-family: $myriadpro-cond-light; }
        &_start-left {
            width: 347px;
            height: 96px;
            padding-right: 36px;
            font-family: $kizo;
            font-size: 44px;
            color: $coral;
            text-align: right;
            line-height: 96px;
            position: absolute;
            top: 13px;
            left: 13px;
            z-index: 1; }
        &_start-right {
            width: 347px;
            height: 96px;
            padding-right: 36px;
            font-family: $kizo;
            font-size: 44px;
            color: $coral;
            text-align: right;
            line-height: 96px;
            position: absolute;
            top: 13px;
            left: 13px;
            z-index: 1; }
        &_winners {
            font-family: $myriadpro-cond;
            font-size: 30px;
            color: $black; } }
    &__frame {
        width: 130px;
        height: 100px;
        &:before {
            content: '';
            display: block;
            width: inherit;
            height: inherit;
            position: relative;
            top: 0;
            left: 0;
            z-index: 1;
            background-color: $black;
            border: 3px solid $pink; }
        &:after {
            top: 9px;
            left: 9px; } }
    &__product {
        min-width: 214px; }
    &__image {
        position: absolute;
        bottom: 3px;
        left: 3px;
        z-index: 2;
        background-repeat: no-repeat;
        &_straight {
            width: 214px;
            height: 113px;
            left: -1px;
            @include image('straight.png'); }
        &_straight_start {
            left: -15px; }
        &_brush {
            width: 190px;
            height: 105px;
            @include image('comb.png'); }
        &_brush_start {
            left: -5px; }
        &_dryer {
            width: 154px;
            height: 105px;
            @include image('dryer.png'); }
        &_dryer_start {
            height: 153px;
            top: -5px;
            left: 20px; }
        &_dryer_start-right {
            left: 35px; } } }

